@include font(Gotham-Bold, Gotham-Bold);
@include font(Gotham-Book, Gotham-Book);
@include font(Gotham-Medium, Gotham-Medium);
$GBK: Gotham-Book, sans-serif;
$GBB: Gotham-Bold, sans-serif;
$GM: Gotham-Medium, sans-serif;

@mixin m {
  @media only screen and (max-width: "767px") {
    @content; } }
@mixin t {
  @media only screen and (max-width: "1139px") {
    @content; } }
@mixin tm {
  @media only screen and (max-width: "991px") {
    @content; } }
* {
	box-sizing: border-box; }
body {
	background: #fff;
	font-family: $GBK;
	font-size: 14px; }
.container {
	max-width: 1140px;
	padding: 0 15px;
	margin-left: auto;
	margin-right: auto;
	position: relative; }
a {
	text-decoration: none; }
.header {
	background: #3a3a3a;
	padding-top: 38px;
	padding-bottom: 358px;
	@include m {
		padding-bottom: 240px; }
	&__container {
		@include clr;
		margin-bottom: 240px;
		@include m {
			margin-bottom: 180px; } }
	&__logo {
		float: left;
		font-size: 0;
		display: block;
		svg {
			font-size: 79px;
			fill: #D1CEC6; } }
	&__menu {
		float: right;
		margin-top: 31px;
		font-size: 0;
		li {
			display: inline-block;
			margin-right: 80px;
			&:last-of-type {
				margin-right: 0; }
			@include m {
				margin-right: 20px; } } }
	&__menu-link {
		color: #fff;
		font-size: 14px;
		text-transform: uppercase;
		letter-spacing: 3px;
		position: relative;
		display: block;
		font-weight: 400;
		&:before {
			position: absolute;
			content: '';
			top: -7px;
			left: -7px;
			bottom: -7px;
			right: -7px; }
		@include rmin(1024) {
			&:after {
				position: absolute;
				top: 0;
				left: 0;
				overflow: hidden;
				max-width: 0;
				border-bottom: 2px solid #e1bb76;
				color: #e1bb76;
				content: attr(data-hover);
				transition: max-width 0.3s; }
			&:hover:after,
			&:focus:after {
				max-width: 98%; } } }
	&__title {
		max-width: 755px;
		margin-left: auto;
		margin-right: auto;
		@include rmin(992) {
			opacity: 0; } }
	&__bg-img {
		position: absolute;
		top: -4%;
		right: 36%;
		transform: translate(50%, -50%);
		z-index: 0;
		opacity: .1;
		@include rmin(992) {
			opacity: 0; }
		@include m {
			right: 50%;
			width: 250px;
			height: 400px;
			top: 50%; } }
	&__decor {
		position: absolute;
		width: 153px;
		height: 173px;
		top: 54%;
		left: 65%;
		transform: translate(-50%, -50%);
		@include tm {
			display: none; } } }
.decor {
	&__square {
		border: 3px solid #ffffff;
		opacity: 0;
		z-index: 2;
		height: 100%;
		position: relative; }
	&__line {
		position: absolute;
		height: 3px;
		//width: 270px
		width: 0;
		background: linear-gradient(90deg, #e8c689,  #897e67 50% , #464646 92%, transparent 100%);
		left: -152px;
		bottom: 62px; } }
.title-lg {
	font-family: $GBK;
	font-size: 30px;
	color: #adadad;
	line-height: 1.5;
	letter-spacing: 10.5px;
	font-weight: 400;
	text-transform: uppercase;
	position: relative;
	z-index: 1;
	@include m {
		font-size: 22px;
		text-align: center;
		br {
			display: none; } }
	span {
		color: #e1bb76; } }
.title-md {
	color: #343434;
	font-family: $GBK;
	font-size: 23px;
	font-weight: 400;
	letter-spacing: 8px;
	line-height: 2.4;
	text-transform: uppercase;
	text-align: center;
	position: relative;
	@include m {
		font-size: 20px; }
	&:after {
		width: 65px;
		height: 1px;
		background: #444444;
		left: 50%;
		transform: translateX(-50%);
		bottom: -18px;
		position: absolute;
		content: '';
		@include rmin(1024) {
			transition: all .3s; } }
	@include rmin(1024) {
		&:hover:after {
			width: 0px; } } }
.services {
