				.services__dropdown {
					display: block;
					//opacity: 1
					//height: auto
					//padding: 7px
 } } } }					//transform: translateY(172px)
	&__item-title {
		color: #666666;
		font-size: 14px;
		font-weight: 400;
		line-height: 17px;
		text-transform: uppercase;
		text-align: center;
		line-height: 1;
		padding-top: 70px;
		padding-left: 0;
		transition: padding-left .25s;
		font-family: $GBK;
		position: relative;
		z-index: 2;
		@include tm {
			padding-left: 0; }
		@include r(1023) {
			padding: 50px 0; } }
	&__bg-letter {
		opacity: 0.06;
		color: #000000;
		font-size: 104px;
		font-weight: 400;
		line-height: 1;
		text-transform: uppercase;
		@include vcenter;
		@include nosel;
		transition: left .25s;
		z-index: 2;
		@include r(1023) {
			top: 19%; } }
	&__topline {
		@include rmin(1024) {
			content: '';
			position: absolute;
			height: 3px;
			left: 215px;
			width: 0;
			background: linear-gradient(90deg, #ebca90 , #f5f5f5);
			top: 35px;
			transform-origin: 0 0;
			transition: width .25s; } }
	&__bottomline {
		@include rmin(1024) {
			position: absolute;
			content: '';
			height: 2px;
			width: 0;
			bottom: 0;
			left: 0;
			background: linear-gradient(90deg, #8f8f8f , #ffb019);
			transition: width .25s; } }
	&__bg {
		@include rmin(1024) {
			//box-shadow: 0 52px 65px rgba(0, 0, 0, 0.09)
			background-color: #fff;
			position: absolute;
			width: 100%;
			height: 0%;
			left: 0;
			bottom: 0;
			z-index: 0;
			transition: height .25s; } }
	&__dropdown {
		display: none;
		position: absolute;
		top: 172px;
		background: #fff;
		//transform: translateY(0)
		z-index: 4;
		width: 100%;
		left: 0;
		padding: 7px;
		//height: 0
		//padding-right: 7px
		//padding-left: 7px
		//transition: all .2s
		vertical-align: top;
		//opacity: 0
		@include r(1023) {
			display: block;
			//height: auto
			position: static;
			top: 0;
			background: none;
 }			//opacity: 1
		p, li {
			font-size: 12px;
			text-align: left;
			margin-bottom: 5px;
			line-height: 1.3;
			color: #000; }
		p,ul {
			line-height: 1.5;
			padding: 15px; } }
	&__droplist {
		list-style-type: disc;
		padding-left: 20px; } }
.clients {
	margin-bottom: 174px;
	@include m {
		margin-bottom: 50px; }
	&__title {
		margin-bottom: 100px; }
	&__list {
		font-size: 0;
		text-align: center; }
	&__item {
		display: inline-block;
		margin: 0 5.5%;
		@include r(799) {
			width: 50%;
			margin: 0 0px 50px; } } }
.map-block {
	&__title {
		margin-bottom: 60px; }
	&__title-sec {
		color: #343434;
		font-family: $GBK;
		font-size: 23px;
		font-weight: 400;
		line-height: 34px;
		text-align: center;
		margin-bottom: 20px;
		line-height: 1; }
	&__wrap {
		padding-top: 40px;
		padding-bottom: 146px;
		background-size: contain;
		background-repeat: no-repeat;
		@include t {
